.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; 
}
.input {
  width: 450px;
}
.li {
  list-style: none;
  margin-bottom: 5px;
}
.arananDiv {
  display: flex;
  flex-direction: row;
  width: 450px;
  align-items: center;
  justify-content: space-between;
  background-color: #f2f2f2;
  padding: 10px;
  margin-bottom: 5px;
  transition: background-color 0.3s;
}
.arananDiv:hover {
  background-color: #d8d8d8;
}
.copyP {
  cursor: pointer;
  background-color: #d2dcd8;
  padding: 3px;
}
.copyP:hover {
  background-color: #a3aba8;
}
